.photo {
  margin: auto;
  height: 36rem;
  border-radius: .2rem;
  background: white;
  display: grid;
  grid-template-columns: 36rem 20rem;
  grid-template-rows: auto 1fr auto;
  overflow: hidden;
  opacity: 0;
  transform: scale(.8);
  animation: scaleUp .5s forwards;
}

.single.photo {
  grid-template-columns: 1fr;
  height: auto;
}

@keyframes scaleUp {
  to {
    opacity: initial;
    transform: initial;
  }
}

.photo::-webkit-scrollbar {
  width: 1.3rem;
  
}

.photo::-webkit-scrollbar-thumb {
  background: #49ce0b;
  border-left: 0.2rem solid #ffffff;
  border-right: 0.2rem solid #ffffff;
}

.photo::-webkit-scrollbar-track {
  background: #ffffff;
}



.details {
  padding: 2rem 2rem 0 2rem;
}

.single .details {
  padding: 1rem 0px 0px 0px ;
}

.img {
  grid-row: 1/4 ;
}

.single.img {
  grid-row: 1;
  border-radius: .4rem;
  overflow: hidden;
  min-height: 100%;
  min-width: 100%;
}

.single.photo::-webkit-scrollbar {
  width: 0px;
}

.comments {
  padding: 0 2rem;
}

@media (max-width: 64rem) {
  .photo {
    height: auto;
    max-height: calc(100vh - 4rem);
    overflow-y: auto;
    grid-template-columns: minmax(10rem, 40rem);
  }
  .img {
    grid-row: 1;
  }
}

.author {
  opacity: .5;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.author a:hover {
  text-decoration: underline;
}

.visualizacoes::before {
  content: '';
  display: inline-block;
  width: 16px;
  height: 10px;
  margin-right: .5rem;
  background: url('../../Assets/visualizacao-black.svg');
}

.attributes {
  display: flex;
  font-size: 1.125rem;
  font-weight: bold;
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.attributes li {
  margin-right: 2rem;
}

.attributes li::before {
  content: '';
  display: inline-block;
  height: 20px;
  margin-right: .5rem;
  position: relative;
  top: 3px;
  width: 2px;
  background: #333;
  margin-top:5px ;
}

.titleContent {

  display: inline-block;
  background-image: linear-gradient(#333 0 0);
  background-position: 0 100%;
  background-size: 0% 2px;
  background-repeat: no-repeat;
  transition:
    background-size 0.3s,
    background-position 0s 0.3s;
  }
  
  .titleContent:hover {
  background-position: 100% 100%;
  background-size: 100% 2px;
  }

  .singleTitle {
    margin-left: 0.35rem;
    
  }

  .singleTitle a {
    cursor:text;
  }